/*!
 * Theme Name: aviscom
 */
@import "destyle.css/destyle.css";
html {
  font-family: 'Roboto', sans-serif;
  color: #2d2d2d;
  font-size: 18px;
  line-height: 1.65;
  font-weight: 300;
}
html,
body {
  overflow-x: hidden;
}
.lines {
  background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAABJMAAAABCAQAAADAmydUAAAAHklEQVR4Ae3BAQ0AAAzDoDqYf7fPfQC1gLeAt4C3AxuqAD3B4Pz6AAAAAElFTkSuQmCC") calc(50% - 1px) 0 repeat-y;
}
@media (max-width: 1219px) {
  .lines {
    background: none;
  }
}
h2 {
  font-size: 36px;
  line-height: 1.35;
  text-align: center;
  font-weight: 500;
  margin-bottom: 64px;
}
@media (max-width: 1219px) {
  h2 {
    margin-bottom: 54px;
  }
}
@media (max-width: 767px) {
  h2 {
    margin-bottom: 43px;
  }
}
h2 span {
  color: #e53641;
}
h3 {
  font-size: 24px;
  line-height: 1.25;
  text-align: center;
  font-weight: 500;
  margin-bottom: 1em;
}
b {
  font-weight: bold;
}
p {
  margin-bottom: 1.55em;
}
a {
  color: #e21d2b;
}
a:hover {
  color: #9e141e;
  text-decoration: underline;
}
ul,
ol {
  margin-bottom: 1.55em;
}
ul li,
ol li {
  margin-bottom: 1em;
  position: relative;
}
ul li {
  padding-left: 22px;
}
ul li:before {
  content: '';
  width: 7px;
  height: 7px;
  background: #e43641;
  border-radius: 50%;
  position: absolute;
  top: 0.6em;
  left: 2px;
}
ol {
  counter-reset: list;
}
ol li {
  padding-left: 24px;
}
ol li:before {
  counter-increment: list;
  content: counter(list) ".";
  position: absolute;
  top: 0;
  left: 2px;
  color: #d73436;
  font-weight: bold;
}
.c {
  padding: 0 10px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1190px;
  width: 100%;
}
@media (max-width: 1219px) {
  .c {
    max-width: 768px;
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .c {
    max-width: 100%;
  }
}
.page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  will-change: filter;
}
.page.mfp-body-open {
  filter: blur(10px);
  overflow: hidden;
}
.lazy {
  visibility: hidden;
  opacity: 0;
}
.lazy.lazy--visible {
  visibility: visible;
  opacity: 1;
}
.hamburger {
  width: 30px;
  height: 3px;
  background: #e00614;
  position: relative;
  margin: 10px;
  margin-right: 0;
  transition: all 0.1s ease-in;
}
.hamburger:before,
.hamburger:after {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  right: 0;
  background: #e00614;
  transition: all 0.1s ease-in;
}
.hamburger:before {
  top: -9px;
}
.hamburger:after {
  bottom: -9px;
}
.hamburger--active {
  width: 40px;
}
.hamburger--active:before {
  width: 60%;
}
.hamburger--active:after {
  width: 50%;
}
img[data-src],
img[data-srcset] {
  min-height: 1px;
}
.hidden {
  display: none;
}
#sprites {
  position: absolute;
  width: 0;
  height: 0;
  top: 0;
  left: 0;
}
.zoom {
  cursor: pointer;
}
.zoom:after {
  content: '';
  display: block;
  position: absolute;
  z-index: 1;
  transition: all 0.2s ease-in;
  left: 50%;
  top: 50%;
  margin-left: -45px;
  margin-top: -45px;
  background: rgba(226,29,43,0.45) url("data:image/png;base64,") 50% 50% no-repeat;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  opacity: 0;
}
.zoom:hover:after {
  opacity: 1;
}
.rub {
  background: url("images/sprite-rouble.png");
}
.rub--red {
  background: url("images/sprite-rouble-red.png");
}
.img {
  border: 2px solid #e43641;
  border-radius: 5px;
}
del,
s {
  text-decoration: line-through;
}
.big {
  font-size: 135%;
}
.center {
  text-align: center;
}
.paroller {
  will-change: transform;
  transition: transform 0.1s ease-out !important;
}
@media (pointer: coarse) {
  .paroller {
    transition: none !important;
  }
}
.icon {
  display: inline-block;
  vertical-align: middle;
  margin-right: 8px;
}
.icon--jaluzi {
  background: url("images/sprite-bigForm-jaluzi.png");
}
.icon--kovannaves {
  background: url("images/sprite-bigForm-kovannaves.png");
}
.icon--lazer {
  background: url("images/sprite-bigForm-lazer.png");
}
.icon--metal {
  background: url("images/sprite-bigForm-metal.png");
}
.icon--naves {
  background: url("images/sprite-bigForm-naves.png");
}
.icon--obicnaves {
  background: url("images/sprite-bigForm-obicnaves.png");
}
.icon--otkat {
  background: url("images/sprite-bigForm-otkat.png");
}
.icon--premnaves {
  background: url("images/sprite-bigForm-premnaves.png");
}
.icon--raspash {
  background: url("images/sprite-bigForm-raspash.png");
}
.icon--vorota {
  background: url("images/sprite-bigForm-vorota.png");
}
.icon--zabor {
  background: url("images/sprite-bigForm-zabor.png");
}
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #e11b2a;
  background: #e11b2a;
  padding: 0 38px;
  position: relative;
  font-weight: bold;
  height: 58px;
  cursor: pointer;
  border-radius: 29px;
  color: #fff;
  transition: all 0.1s ease-in !important;
}
.btn svg {
  display: block;
  fill: #fff;
  transition: all 0.1s ease-in;
}
.btn:focus {
  outline: 0;
}
.btn:hover,
.btn--active {
  text-decoration: none;
  background: #fff;
  border-color: #e43641;
  color: #2a2a2a;
}
.btn:hover svg,
.btn--active svg {
  fill: #000;
}
.btn--sm {
  height: 50px;
  padding: 0 27px;
  border-radius: 25px;
}
.btn--round {
  width: 52px;
  height: 52px;
  padding: 0;
  align-items: center;
  justify-content: center;
}
.btn--round.btn--sm {
  width: 37px;
  height: 37px;
}
.btn--block {
  width: 100%;
  padding: 0;
}
.btn--inverse {
  background: #fff;
  color: #2a2a2a;
  font-size: 16px;
  border-color: #e43641;
}
.btn--inverse:hover {
  border: 1px solid #e11b2a;
  background: #e11b2a;
  color: #fff;
}
.btn--arrow-prev svg,
.btn--arrow-next svg {
  width: 12px;
  height: 22px;
}
.btn--arrow-prev svg {
  margin-right: 4px;
}
.btn--arrow-next svg {
  transform: rotate(180deg);
  margin-left: 4px;
}
.input__input {
  display: block;
  width: 100%;
  background: #f3f3f3;
  border: 1px solid #f3f3f3;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}
.input__input--big {
  padding: 15px 25px;
}
.input__input--sm {
  padding: 7px 14px;
}
.input__input--textarea {
  resize: none;
  min-width: 100%;
  max-width: 100%;
  line-height: 1.2;
}
.input__input.wpcf7-not-valid {
  border-color: #f00;
}
.radio {
  cursor: pointer;
}
.radio input {
  display: none;
}
.radio__label {
  position: relative;
  display: flex;
  align-items: center;
  transition: all 0.1s ease-in;
}
.radio__label:not(:empty):before {
  margin-right: 16px;
}
.radio__label:before,
.radio__label:after {
  content: '';
  display: block;
}
.radio__label:before {
  width: 25px;
  height: 25px;
  border-radius: 2px;
  background: #f3f3f3;
}
.radio__label:after {
  position: absolute;
  left: 8px;
  top: 8px;
  width: 11px;
  height: 11px;
  border-radius: 50%;
  background: url("data:image/svg+xml;base64,PHN2ZyB2aWV3Qm94PSIwIDAgNDQ4LjggMzQxLjciIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+PHBhdGggZD0iTTE0Mi44IDI3MC4zTDM1LjcgMTYzLjIgMCAxOTguOWwxNDIuOCAxNDIuOCAzMDYtMzA2TDQxMy4xIDB6Ii8+PC9zdmc+") 50% 50% no-repeat;
  opacity: 0;
  transition: all 0.1s ease-in;
}
input:checked ~ .radio__label {
  font-weight: 500;
}
input:checked ~ .radio__label:after {
  opacity: 1;
}
.radio--warn {
  color: #e53641;
}
.panel {
  border: 1px solid #f0f0f0;
  border-radius: 5px;
  position: relative;
  background: #fff;
  box-shadow: 0 10px 25px -10px #e4dede;
  margin: 0 auto;
}
.panel:after {
  content: '';
  display: block;
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 15px;
  background: #e21d2b;
  border-radius: 5px;
  z-index: -1;
}
.panel__img {
